import React, { useState } from "react";
import * as XLSX from "xlsx";
import { saveAs } from "file-saver";

const classLinks = {
  "Class 1": "https://drive.google.com/file/d/EXAMPLE_CLASS1_ID/view?usp=sharing",
    "Class 2": "https://drive.google.com/file/d/EXAMPLE_CLASS2_ID/view?usp=sharing",
      "Class 3": "https://drive.google.com/file/d/EXAMPLE_CLASS3_ID/view?usp=sharing",
        "Class 4": "https://drive.google.com/file/d/EXAMPLE_CLASS4_ID/view?usp=sharing",
          "Class 5": "https://docs.google.com/spreadsheets/d/116Chd4DFegni0wU-_7q2iXBnh1G7Jeuu/view?usp=sharing",
            "Class 6": "https://drive.google.com/file/d/EXAMPLE_CLASS6_ID/view?usp=sharing",
              "JHS 1": "https://docs.google.com/spreadsheets/d/116Chd4DFegni0wU-_7q2iXBnh1G7Jeuu/edit?usp=drivesdk&ouid=101670710374890442267&rtpof=true&sd=true/view?usp=sharing",
                "JHS 2": "https://drive.google.com/file/d/EXAMPLE_JHS2_ID/view?usp=sharing",
                  "JHS 3": "https://drive.google.com/file/d/EXAMPLE_JHS3_ID/view?usp=sharing",
                  };

                  const Assessment = () => {
                    const [data, setData] = useState([]);
                      const [fileName, setFileName] = useState("Edited-Assessment.xlsx");
                        const [selectedClass, setSelectedClass] = useState("");
                          const [loading, setLoading] = useState(false);

                            const extractFileId = (link) => {
                                const match = link.match(/\/d\/([a-zA-Z0-9-_]+)/);
                                    return match ? match[1] : null;
                                      };

                                        const fetchFromClass = async (cls) => {
                                            const link = classLinks[cls];
                                                if (!link) return;

                                                    const fileId = extractFileId(link);
                                                        const url = `https://drive.google.com/uc?export=download&id=${fileId}`;
                                                            setFileName(cls + "-Assessment.xlsx");
                                                                await fetchAndParseExcel(url);
                                                                  };

                                                                    const fetchAndParseExcel = async (url) => {
                                                                        setLoading(true);
                                                                            try {
                                                                                  const res = await fetch(url);
                                                                                        const buffer = await res.arrayBuffer();
                                                                                              parseExcel(buffer);
                                                                                                  } catch (err) {
                                                                                                        alert("Error loading Excel file.");
                                                                                                            }
                                                                                                                setLoading(false);
                                                                                                                  };

                                                                                                                    const parseExcel = (buffer) => {
                                                                                                                        const workbook = XLSX.read(buffer, { type: "buffer" });
                                                                                                                            const worksheet = workbook.Sheets[workbook.SheetNames[0]];
                                                                                                                                const jsonData = XLSX.utils.sheet_to_json(worksheet, { header: 1, blankrows: true });
                                                                                                                                    setData(jsonData);
                                                                                                                                      };

                                                                                                                                        const handleEdit = (row, col, value) => {
                                                                                                                                            const updated = [...data];
                                                                                                                                                updated[row][col] = value;
                                                                                                                                                    setData(updated);
                                                                                                                                                      };

                                                                                                                                                        const handleDownload = () => {
                                                                                                                                                            const worksheet = XLSX.utils.aoa_to_sheet(data);
                                                                                                                                                                const workbook = XLSX.utils.book_new();
                                                                                                                                                                    XLSX.utils.book_append_sheet(workbook, worksheet, "Sheet1");
                                                                                                                                                                        const excelBuffer = XLSX.write(workbook, {
                                                                                                                                                                              bookType: "xlsx",
                                                                                                                                                                                    type: "array",
                                                                                                                                                                                        });

                                                                                                                                                                                            const blob = new Blob([excelBuffer], {
                                                                                                                                                                                                  type: "application/vnd.openxmlformats-officedocument.spreadsheetml.sheet",
                                                                                                                                                                                                      });

                                                                                                                                                                                                          saveAs(blob, fileName);
                                                                                                                                                                                                            };

                                                                                                                                                                                                              return (
                                                                                                                                                                                                                  <div style={styles.container}>
                                                                                                                                                                                                                        <h2 style={styles.title}>Class Assessment Excel Editor</h2>

                                                                                                                                                                                                                              {/* Class Dropdown */}
                                                                                                                                                                                                                                    <div style={styles.row}>
                                                                                                                                                                                                                                            <select
                                                                                                                                                                                                                                                      style={styles.select}
                                                                                                                                                                                                                                                                value={selectedClass}
                                                                                                                                                                                                                                                                          onChange={(e) => {
                                                                                                                                                                                                                                                                                      const cls = e.target.value;
                                                                                                                                                                                                                                                                                                  setSelectedClass(cls);
                                                                                                                                                                                                                                                                                                              fetchFromClass(cls);
                                                                                                                                                                                                                                                                                                                        }}
                                                                                                                                                                                                                                                                                                                                >
                                                                                                                                                                                                                                                                                                                                          <option value="">Select a class</option>
                                                                                                                                                                                                                                                                                                                                                    {Object.keys(classLinks).map((cls) => (
                                                                                                                                                                                                                                                                                                                                                                <option key={cls} value={cls}>
                                                                                                                                                                                                                                                                                                                                                                              {cls}
                                                                                                                                                                                                                                                                                                                                                                                          </option>
                                                                                                                                                                                                                                                                                                                                                                                                    ))}
                                                                                                                                                                                                                                                                                                                                                                                                            </select>
                                                                                                                                                                                                                                                                                                                                                                                                                  </div>

                                                                                                                                                                                                                                                                                                                                                                                                                        {/* Table View */}
                                                                                                                                                                                                                                                                                                                                                                                                                              {loading && <p>Loading Excel file...</p>}
                                                                                                                                                                                                                                                                                                                                                                                                                                    {data.length > 0 && (
                                                                                                                                                                                                                                                                                                                                                                                                                                            <>
                                                                                                                                                                                                                                                                                                                                                                                                                                                      <div style={{ overflowX: "auto", marginTop: 20 }}>
                                                                                                                                                                                                                                                                                                                                                                                                                                                                  <table style={styles.table}>
                                                                                                                                                                                                                                                                                                                                                                                                                                                                                <tbody>
                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                {data.map((row, rowIndex) => (
                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                  <tr key={rowIndex}>
                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                      {row.map((cell, colIndex) => (
                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                            <td key={colIndex}>
                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                    <input
                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                              type="text"
                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                        value={cell || ""}
                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                  onChange={(e) => handleEdit(rowIndex, colIndex, e.target.value)}
                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                            style={styles.cellInput}
                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                    />
                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                          </td>
                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                              ))}
                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                </tr>
                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                ))}
                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                              </tbody>
                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                          </table>
                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                    </div>
                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                              <button style={styles.downloadButton} onClick={handleDownload}>
                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                          Download Edited File
                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                    </button>
                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                            </>
                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                  )}
                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                      </div>
                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                        );
                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                        };

                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                        const styles = {
                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                          container: {
                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                              maxWidth: 1000,
                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                  margin: "0 auto",
                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                      padding: 20,
                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                          fontFamily: "Segoe UI, sans-serif",
                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                              backgroundColor: "#f9f9f9",
                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                  borderRadius: 10,
                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                      boxShadow: "0 0 10px rgba(0,0,0,0.1)",
                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                        },
                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                          title: {
                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                              textAlign: "center",
                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                  color: "#333",
                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                    },
                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                      row: {
                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                          display: "flex",
                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                              justifyContent: "center",
                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                  marginTop: 10,
                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                    },
                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                      select: {
                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                          padding: 10,
                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                              fontSize: 16,
                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                  borderRadius: 6,
                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                      border: "1px solid #ccc",
                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                          width: 300,
                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                            },
                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                              table: {
                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                  borderCollapse: "collapse",
                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                      width: "100%",
                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                          backgroundColor: "#fff",
                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                            },
                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                              cellInput: {
                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                  width: "100px",
                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                      padding: 5,
                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                          border: "1px solid #ddd",
                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                              fontSize: 14,
                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                },
                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                  downloadButton: {
                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                      marginTop: 20,
                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                          padding: "10px 20px",
                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                              backgroundColor: "#28a745",
                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                  color: "#fff",
                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                      fontSize: 16,
                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                          border: "none",
                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                              borderRadius: 6,
                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                  cursor: "pointer",
                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                    },
                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                    };

                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                    export default Assessment;